import React, { useState, useEffect, useImperativeHandle, forwardRef } from 'react'
import { View, Text, ScrollView } from '@tarojs/components'
import { therapistService } from '@/services/therapist'
import './index.scss'

interface Service {
  id: string
  name: string
  duration: number
  price: number
  discountPrice?: number
}

interface TimeSlot {
  time: string
  available: boolean
  status?: 'available' | 'busy' | 'break'
}

interface BookingSelectorProps {
  services: Service[]
  therapistId?: string
  onServiceSelect: (service: Service) => void
  onTimeSelect: (date: string, time: string) => void
}

export interface BookingSelectorHandle {
  clearSelectedTime: () => void
}

const BookingSelector = forwardRef<BookingSelectorHandle, BookingSelectorProps>(({
  services,
  therapistId,
  onServiceSelect,
  onTimeSelect
}, ref) => {
  const [selectedServiceId, setSelectedServiceId] = useState<string>('')
  const [selectedService, setSelectedService] = useState<Service | null>(null)
  const [selectedDate, setSelectedDate] = useState<string>('')
  const [selectedTime, setSelectedTime] = useState<string>('')
  const [timeSlots, setTimeSlots] = useState<TimeSlot[][]>([])  // Â≠òÂÇ®‰ªéAPIËé∑ÂèñÁöÑÊó∂Èó¥ÊÆµ
  const [loadingSlots, setLoadingSlots] = useState(false)

  // Êö¥Èú≤ÊñπÊ≥ïÁªôÁà∂ÁªÑ‰ª∂
  useImperativeHandle(ref, () => ({
    clearSelectedTime: () => {
      setSelectedTime('')
    }
  }), [])

  // ÂΩìÈÄâÊã©Êó•ÊúüÂíåÊúçÂä°Êó∂ÔºåËé∑ÂèñÂèØÁî®Êó∂ÊÆµ
  useEffect(() => {
    if (selectedDate && therapistId && selectedService) {
      fetchAvailableSlots()
    }
  }, [selectedDate, selectedService, therapistId])

  // È°µÈù¢ÈáçÊñ∞ÂèØËßÅÊó∂Âà∑Êñ∞Êó∂Èó¥ÊÆµÊï∞ÊçÆ
  useEffect(() => {
    const handleVisibilityChange = () => {
      if (!document.hidden && selectedDate && therapistId && selectedService) {
        console.log('È°µÈù¢ÈáçÊñ∞ÂèØËßÅÔºåÂà∑Êñ∞Êó∂Èó¥ÊÆµÊï∞ÊçÆ')
        fetchAvailableSlots()
      }
    }

    document.addEventListener('visibilitychange', handleVisibilityChange)
    return () => document.removeEventListener('visibilitychange', handleVisibilityChange)
  }, [selectedDate, therapistId, selectedService])

  const fetchAvailableSlots = async () => {
    if (!therapistId || !selectedDate || !selectedService) return

    setLoadingSlots(true)
    try {
      const result = await therapistService.getAvailableSlots(
        therapistId,
        selectedDate,
        selectedService.duration
      )

      console.log('Ëé∑ÂèñÂà∞ÁöÑÂèØÁî®Êó∂ÊÆµÊï∞ÊçÆÔºö', result)

      // Â¶ÇÊûúAPIËøîÂõû‰∫ÜÊúâÊïàÁöÑslotsÊï∞ÊçÆ
      if (result && result.slots && result.slots.length > 0) {
        // ‰ΩøÁî®APIËøîÂõûÁöÑÊï∞ÊçÆ
        const grid = []

        // ÂàõÂª∫‰∏Ä‰∏™Êò†Â∞ÑÔºåÊñπ‰æøÊü•ÊâæÊï¥ÁÇπÁöÑÂèØÁî®ÊÄß
        const hourAvailability = new Map()
        result.slots.forEach(slot => {
          const hour = slot.time.split(':')[0]
          hourAvailability.set(hour, { available: slot.available, status: slot.status })
        })

        for (let hour = 9; hour <= 21; hour++) {
          const hourStr = hour.toString().padStart(2, '0')
          const hourSlots = []

          // Ëé∑ÂèñËØ•Â∞èÊó∂ÁöÑÂèØÁî®ÊÄßÔºà‰ªéÊï¥ÁÇπÊï∞ÊçÆÊé®Êñ≠Ôºâ
          const hourData = hourAvailability.get(hourStr) || { available: true, status: 'available' }

          for (let minute = 0; minute < 60; minute += 10) {
            const time = `${hourStr}:${minute.toString().padStart(2, '0')}`

            // Â¶ÇÊûúÊòØÊï¥ÁÇπÔºåÁõ¥Êé•‰ΩøÁî®APIËøîÂõûÁöÑÊï∞ÊçÆ
            // Â¶ÇÊûú‰∏çÊòØÊï¥ÁÇπÔºå‰ΩøÁî®ËØ•Â∞èÊó∂Êï¥ÁÇπÁöÑÂèØÁî®ÊÄß
            const slot = result.slots.find(s => s.time === time)

            if (slot) {
              // Â¶ÇÊûúAPIËøîÂõû‰∫ÜËøô‰∏™ÂÖ∑‰ΩìÊó∂Èó¥ÁÇπÁöÑÊï∞ÊçÆÔºå‰ΩøÁî®ÂÆÉ
              hourSlots.push({
                time,
                available: slot.available,
                status: slot.status
              })
            } else {
              // Âê¶Âàô‰ΩøÁî®ËØ•Â∞èÊó∂Êï¥ÁÇπÁöÑÂèØÁî®ÊÄß
              hourSlots.push({
                time,
                available: hourData.available,
                status: hourData.status
              })
            }
          }
          grid.push(hourSlots)
        }
        setTimeSlots(grid)
      } else {
        // Â¶ÇÊûúAPIÊ≤°ÊúâËøîÂõûÊúâÊïàÊï∞ÊçÆÔºåÈªòËÆ§ÂÖ®ÈÉ®ÂèØÁî®
        console.log('APIÊú™ËøîÂõûÊúâÊïàÊó∂ÊÆµÊï∞ÊçÆÔºå‰ΩøÁî®ÈªòËÆ§ÂÖ®ÈÉ®ÂèØÁî®')
        const grid = []
        for (let hour = 9; hour <= 21; hour++) {
          const hourSlots = []
          for (let minute = 0; minute < 60; minute += 10) {
            const time = `${hour.toString().padStart(2, '0')}:${minute.toString().padStart(2, '0')}`
            hourSlots.push({
              time,
              available: true,
              status: 'available' as const
            })
          }
          grid.push(hourSlots)
        }
        setTimeSlots(grid)
      }
    } catch (error) {
      console.error('Failed to fetch available slots:', error)
      // Â¶ÇÊûúËé∑ÂèñÂ§±Ë¥•Ôºå‰ΩøÁî®ÈªòËÆ§ÂÖ®ÈÉ®ÂèØÁî®
      const grid = []
      for (let hour = 9; hour <= 21; hour++) {
        const hourSlots = []
        for (let minute = 0; minute < 60; minute += 10) {
          const time = `${hour.toString().padStart(2, '0')}:${minute.toString().padStart(2, '0')}`
          hourSlots.push({
            time,
            available: true,
            status: 'available' as const
          })
        }
        grid.push(hourSlots)
      }
      setTimeSlots(grid)
    } finally {
      setLoadingSlots(false)
    }
  }

  // ÁîüÊàêÊó•ÊúüÂàóË°®Ôºà‰ªäÂ§©+Êé•‰∏ãÊù•4Â§©Ôºâ
  const generateDateList = () => {
    const dates = []
    const today = new Date()
    
    for (let i = 0; i < 5; i++) {
      const date = new Date(today)
      date.setDate(today.getDate() + i)
      
      const month = date.getMonth() + 1
      const day = date.getDate()
      const weekDays = ['Âë®Êó•', 'Âë®‰∏Ä', 'Âë®‰∫å', 'Âë®‰∏â', 'Âë®Âõõ', 'Âë®‰∫î', 'Âë®ÂÖ≠']
      const weekDay = weekDays[date.getDay()]
      
      dates.push({
        key: date.toISOString().split('T')[0],
        display: i === 0 ? '‰ªäÂ§©' : `${month}Êúà${day}Êó•`,
        weekDay: i === 0 ? '' : weekDay
      })
    }
    
    return dates
  }

  // ÁîüÊàêÊó∂Èó¥ÁΩëÊ†ºÊï∞ÊçÆÔºàÊåâÂ∞èÊó∂ÂàÜÁªÑÔºâ
  const generateTimeGrid = () => {
    // Â¶ÇÊûúÊúâ‰ªéAPIËé∑ÂèñÁöÑÊï∞ÊçÆÔºå‰ΩøÁî®APIÊï∞ÊçÆ
    if (timeSlots.length > 0) {
      const grid = timeSlots.map((hourSlots, index) => {
        const hour = 9 + index
        return {
          hour: `${hour}:00`,
          slots: hourSlots
        }
      })
      return grid
    }

    // Âê¶ÂàôËøîÂõûÈªòËÆ§ÁöÑÂÖ®ÈÉ®ÂèØÁî®Êó∂ÊÆµ
    const grid = []
    for (let hour = 9; hour <= 21; hour++) {
      const hourSlots = []
      for (let minute = 0; minute < 60; minute += 10) {
        const time = `${hour.toString().padStart(2, '0')}:${minute.toString().padStart(2, '0')}`
        hourSlots.push({
          time,
          available: true
        })
      }
      grid.push({
        hour: `${hour}:00`,
        slots: hourSlots
      })
    }
    return grid
  }

  // Âà§Êñ≠Êüê‰∏™Êó∂Èó¥ÊßΩÊòØÂê¶Ë¢´ÈÄâ‰∏≠
  const isTimeSlotSelected = (time: string) => {
    if (!selectedTime || !selectedService) return false
    
    const startTime = selectedTime
    const duration = selectedService.duration
    
    // Â∞ÜÊó∂Èó¥ËΩ¨Êç¢‰∏∫ÂàÜÈíüÊï∞ËøõË°åËÆ°ÁÆó
    const timeToMinutes = (timeStr: string) => {
      const [hour, minute] = timeStr.split(':').map(Number)
      return hour * 60 + minute
    }
    
    const startMinutes = timeToMinutes(startTime)
    const currentMinutes = timeToMinutes(time)
    const endMinutes = startMinutes + duration
    
    return currentMinutes >= startMinutes && currentMinutes < endMinutes
  }

  const handleServiceSelect = (service: Service) => {
    setSelectedServiceId(service.id)
    setSelectedService(service)
    onServiceSelect(service)
    // ÈáçÁΩÆÊó∂Èó¥ÈÄâÊã©
    setSelectedTime('')
  }

  const handleDateSelect = (dateKey: string) => {
    setSelectedDate(dateKey)
    // ÈáçÁΩÆÊó∂Èó¥ÈÄâÊã©ÂíåÊó∂Èó¥ÊÆµÊï∞ÊçÆ
    setSelectedTime('')
    setTimeSlots([])  // Ê∏ÖÁ©∫‰πãÂâçÁöÑÊó∂Èó¥ÊÆµÊï∞ÊçÆÔºåËß¶ÂèëÈáçÊñ∞Ëé∑Âèñ
  }

  const handleTimeSelect = (time: string, available: boolean) => {
    if (!available || !selectedDate || !selectedService) return
    
    // Ê£ÄÊü•ÈÄâÊã©ÁöÑÊó∂Èó¥ÊÆµÊòØÂê¶Ë∂≥Â§ü
    const timeToMinutes = (timeStr: string) => {
      const [hour, minute] = timeStr.split(':').map(Number)
      return hour * 60 + minute
    }
    
    const startMinutes = timeToMinutes(time)
    const endMinutes = startMinutes + selectedService.duration
    
    // Ê£ÄÊü•ÊòØÂê¶Ë∂ÖËøáËê•‰∏öÊó∂Èó¥
    if (endMinutes > 22 * 60) { // 22:00
      return
    }
    
    setSelectedTime(time)
    onTimeSelect(selectedDate, time)
    
    // Ëá™Âä®ÂºπÂá∫Ë¥≠Áâ©ËΩ¶
    setTimeout(() => {
      const cartBtn = document.querySelector('.checkout-btn:not(.disabled)')
      if (cartBtn) {
        (cartBtn as HTMLElement).click()
      }
    }, 300)
  }

  const dateList = generateDateList()
  const timeGrid = generateTimeGrid()

  return (
    <View className="booking-selector">
      {/* ÊúçÂä°ÈÄâÊã© */}
      <View className="service-section">
        <View className="section-title">ÈÄâÊã©ÊúçÂä°</View>
        <ScrollView className="service-tabs" scrollX>
          {services.map(service => (
            <View
              key={service.id}
              className={`service-tab ${selectedServiceId === service.id ? 'active' : ''}`}
              onClick={() => handleServiceSelect(service)}
            >
              <Text className="service-name">{service.name}</Text>
              <View className="service-info">
                <Text className="service-duration">{service.duration}ÂàÜÈíü</Text>
                <Text className="price">¬•{service.discountPrice || service.price}</Text>
              </View>
            </View>
          ))}
        </ScrollView>
      </View>

      {/* Êó•ÊúüÊó∂Èó¥ÈÄâÊã© */}
      {selectedServiceId && (
        <View className="datetime-section">
          {/* Êó•ÊúüÈÄâÊã© */}
          <ScrollView className="date-tabs" scrollX>
            {dateList.map(date => (
              <View
                key={date.key}
                className={`date-tab ${selectedDate === date.key ? 'active' : ''}`}
                onClick={() => handleDateSelect(date.key)}
              >
                <Text className="date-display">{date.display}</Text>
                {date.weekDay && <Text className="week-day">{date.weekDay}</Text>}
              </View>
            ))}
          </ScrollView>

          {/* Êó∂Èó¥ÊÆµÈÄâÊã© */}
          {selectedDate && (
            <ScrollView className="time-grid-container" scrollY>
              <View className="time-grid-wrapper">
                {loadingSlots ? (
                  <View className="loading-slots">
                    <Text>Âä†ËΩΩÂèØÁî®Êó∂ÊÆµ...</Text>
                  </View>
                ) : (
                  timeGrid.map((row, rowIndex) => (
                  <View key={rowIndex} className="time-row">
                    <Text className="hour-label">{row.hour}</Text>
                    <View className="time-slots">
                      {row.slots.map((slot, slotIndex) => (
                        <View
                          key={slotIndex}
                          className={`time-slot ${
                            slot.available 
                              ? isTimeSlotSelected(slot.time)
                                ? 'selected' 
                                : 'available'
                              : 'disabled'
                          }`}
                          onClick={() => handleTimeSelect(slot.time, slot.available)}
                        >
                          <Text className="time-text">:{slot.time.split(':')[1]}</Text>
                        </View>
                      ))}
                    </View>
                  </View>
                ))
                )}
              </View>
            </ScrollView>
          )}
        </View>
      )}
    </View>
  )
})

BookingSelector.displayName = 'BookingSelector'

export default BookingSelector